/*Setting farming products*/

.container-farming-products{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 50px;
}

.title-farming-catalogue{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
    padding-left: 25px;
    padding-top: 25px;
    padding-bottom: 50px;
    color: #000;
}

.product-farming-item{
    width: 15%;
    height: 275px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px 15px;
    border: 2px solid rgb(230, 230, 230);
    cursor: pointer;
}

.product-farming-item:hover{
    border: none;
    box-shadow: 2px 2px 2px 1px gray;
}

.bacter-sp {
    width: 185px;
}

.bacter-sm {
    width: 160px;
}

.polisulfuro {
    width: 160px;
}

.abono {
    width: 150px;
}

/*Setting garden products*/

.container-garden-products{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 50px;
}

.title-garden-catalogue{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
