    color: #000;
}

.product-garden-item{
    width: 15%;
    height: 275px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px 15px;
    border: 2px solid rgb(230, 230, 230);
    cursor: pointer;
}

.product-garden-item:hover{
    border: none;
    box-shadow: 2px 2px 2px 1px gray;
}

.bacter-sp-gr{
    width: 95px;
}

.bacter-sm-gr{
    width: 110px;
}

.carbon-activado-gr{
    width: 175px;
}

.fungicida-gr{
    width: 175px;
}

.enraizador-gr{
    width: 110px;
}

.huerto-gr{
    width: 190px;
}

.compostera-g{
    width: 200px;
}

.abono-gr {
    width: 150px;
}

.name-product{
    font-family: 'Titillium Web', sans-serif;
    font-size: 20px;
    color: #000;
    font-weight: bolder;
}

