/*Setting our custumer section*/

.customer-title{
    font-family: 'Titillium Web', sans-serif;
    font-size: 36px;
    padding-left: 25px;
    padding-top: 25px;
    padding-bottom: 50px;
    color: #000;
}

.container-customer-img{
    width: 90%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.orquideas-img{
    width: 25%;
}

.botanika{
    font-family: 'Bebas Neue', cursive;
    font-size: 50px;
}

/*-------------------------Media Query max-width: 550px-------------------------*/

@media (max-width: 550px){

/*Setting our custumer section*/

.customer-title{
    font-size: 30px;
}

.container-customer-img{
    flex-direction: column;
}

.orquideas-img{
    width: 70%;
}

.caoba-img, .botanika, .orquideas-img{
    margin-bottom: 50px;
}

.botanika{
    font-family: 'Bebas Neue', cursive;
    font-size: 30px;
    font-weight: bolder;
}

}

/*-------------------------Media Query min-width: 550px-------------------------*/

@media (min-width: 550px){

/*Setting our custumer section*/

.caoba-img{
    width: 25%;
}

.orquideas-img{
    width: 30%;
}

.botanika{
    text-align: center;
}

.caoba-img, .botanika, .orquideas-img{
    margin-bottom: 50px;
}

}